.admin-report {
  .conditional-loading-section {
    &.is-loading {
      margin: 0;
    }
  }

  .header {
    display: flex;
    align-items: center;
    border-bottom: 1px solid var(--primary-low);
    margin-bottom: 0.5em;
    padding-bottom: 0.5em;
  }

  .header .breadcrumb {
    margin: 0;
    list-style: none;

    .item {
      display: inline;
      font-size: $font-up-1;
    }

    .all-reports .report-url {
      font-weight: 700;
    }

    .report {
      font-weight: 700;

      .report-url {
        color: var(--primary);
      }

      .info {
        cursor: pointer;
        margin-left: 0.25em;
        color: var(--primary-low-mid);

        &:hover {
          color: var(--primary-medium);
        }
      }
    }
  }

  .header .trend {
    margin-left: auto;
    margin-right: 8px;

    &.trending-down,
    &.high-trending-down {
      color: var(--danger);
    }

    &.trending-up,
    &.high-trending-up {
      color: var(--success);
    }

    &.no-change {
      color: var(--primary-medium);
      .d-icon {
        display: none;
      }
    }

    .value {
      font-size: $font-up-1;
    }

    .icon {
      font-size: $font-up-1;
      font-weight: 700;
    }
  }

  .body {
    display: flex;
  }

  .main {
    flex: 1;
  }

  .main .report-alert {
    margin: 0;
    text-align: center;
    padding: 3em;
    border: 1px solid transparent;

    a {
      color: var(--primary-medium);
    }

    .d-icon {
      color: currentColor;
      font-size: $font-up-5;
      display: block;
      margin: 0.25em auto;
    }

    &.no-data,
    &.rate-limited {
      background: var(--secondary);
      border-color: var(--primary-low);
      color: var(--primary-low-mid);
    }

    &.rate-limited .d-icon {
      color: var(--danger);
    }

    &.timeout,
    &.exception {
      border-color: var(--danger-low);
      color: var(--danger);
    }
  }

  .average-chart {
    padding: 0.5em;
    border: 1px solid var(--highlight-high);
    color: var(--highlight-high);
    margin-bottom: 1em;
    text-align: center;
    background: var(--highlight-low);
  }

  .filters {
    display: flex;
    margin-left: 1em;
    flex-direction: column;
    width: 220px;

    .modes {
      margin: 0 0 1em 0;
      padding: 0;
      list-style: none;

      .mode {
        display: inline-flex;
        flex: 1 0 0px;

        .mode-btn.is-current {
          color: var(--tertiary);
        }
      }
    }

    .chart-groupings {
      display: grid;
      grid-template-columns: repeat(auto-fill, 1fr);
      grid-gap: 0.5em;
      margin-bottom: 1em;

      .chart-grouping.active {
        background: var(--tertiary);
        color: var(--secondary);
      }
    }

    .control {
      margin-bottom: 1em;
    }

    .control .label {
      font-weight: 700;
      width: 100%;
    }

    .control .input,
    .control .select-kit {
      width: 100%;

      .export-csv-btn {
        width: 100%;
      }

      .refresh-report-btn {
        width: 100%;
      }
    }
  }
}

.rtl .admin-report {
  .filters {
    margin-left: 0;
    margin-right: 1em;
  }

  .trend {
    margin-left: unset;
    margin-right: auto;
  }
}

.admin-report.storage-stats {
  .main {
    flex: 1 0 auto;
  }
}
